import React from 'react';
import Button from '@material-ui/core/Button';
import { Avatar, Box} from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import "./footer.css"
import image from "../image/icon.png"
import Playstore from '../image/playstore.jpg'
import { blue } from '@material-ui/core/colors';






    const Footer = () => {

       


        return ( 

      <Box >
             
              <Box  style={{marginTop:80,backgroundColor:blue[50]}}>


                <Box display="flex" style={{height:390, marginTop:-80}}>
                        
                        <Box>
                                <Avatar img src={image} 
                                style={{
                                backgroundColor:blue[50],
                                marginTop:-10,
                                margin: 50,
                                width: 200,
                                height: 200,
                                marginLeft:150,
                               
                                
                                }} />
                        </Box>
                                                        
                        
                        <Box>
                                                                
                        <Typography variant="h2" style={{color:"orange",marginTop:100,marginLeft:-150}}>Construction Manager</Typography>

                                <Typography variant="h4" 
                                        style={{
                                                marginTop:70,
                                                marginLeft:-200,
                                                color:"black"
                                                }}
                                        >One Solution For Managing Multiple Construction Site
                                </Typography>
                        </Box>

                </Box>


                <Box style={{marginLeft:1200, marginTop:-300}}>
                        <Button>
                          <a href="https://www.facebook.com/official.forbit" class="fa fa-facebook"></a>
                        </Button>

                        <Button>
                          <a href="https://www.youtube.com/channel/UCxIrxt-lY36xmvPFzgEUgTw" class="fa fa-youtube"></a>
                        </Button>

                        <Button>
                          <a href="https://play.google.com/store/apps/details?id=com.forbitbd.constructionmanager"><img src={Playstore} style={{height:60,width:80}}/></a>
                        </Button>

                </Box>

               
                </Box>
             
          <Box  className="footer" display="flex" style={{height:"200 ", width:"300",marginTop:-15}}>
        
               

          </Box>
      </Box>
         );
    }
     
    export default Footer;
