import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import image from "../image/icon.png"
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import { Link } from "react-scroll";
import {Box } from '@material-ui/core';
import { grey } from "@material-ui/core/colors";




const Navbar = () => {
  return (
   
       <Box>
            <AppBar
          style={{
            backgroundColor:"black",
            boxShadow: "initial",
          }}
        >
          <Toolbar>
           <Box style={{padding:5}}>  
           <img src={image} alt="logo" style={{ height: "60px",backgroundColor:grey[900],borderRadius:1}} />
           </Box>

            <Grid
              container
              direction="row"
              justify="flex-end"
              alignItems="center"
            >
              <Button  style={{color:'orange',marginRight:60 }}>
                <Link
                  style={{ activeClass: "1px solid black" }}
                  activeClass="active"
                  to="marginTop"
                  spy={true}
                  smooth={true}
                  offset={-70}
                  duration={500}
                >
                  Home
                </Link>
              </Button>
              <Button  style={{color:'orange',marginRight:60}}>
                <Link
                  style={{ activeClass: "1px solid black" }}
                  activeClass="active"
                  to="features"
                  spy={true}
                  smooth={true}
                  offset={-70}
                  duration={500}
                >
                  Feature
                </Link>
              </Button>

              <Button  style={{color:'orange',marginRight:60}}>
                {" "}
                <Link
                  activeClass="active"
                  to="screenshot"
                  spy={true}
                  smooth={true}
                  offset={-70}
                  duration={500}
                >
                  ScreenShot
                </Link>
              </Button>

              <Button  style={{color:'orange',marginRight:60}}>
                {" "}
                <Link
                  activeClass="active"
                  to="about"
                  spy={true}
                  smooth={true}
                  offset={-70}
                  duration={500}
                >
                  About Us
                </Link>
              </Button>

              <Button href="https://play.google.com/store/apps/details?id=com.forbitbd.constructionmanager" style={{color:'orange',marginRight:60}}>
                
                 Download App
              </Button>

              
              
              <Button  style={{color:'orange',marginRight:60}}> Login</Button>
            </Grid>
          </Toolbar>
        </AppBar>
      <Toolbar />
       </Box>
    
  );
};

export default Navbar;